.card {
  background: rgb(245, 0, 0);
  margin-left: 50px;
  border: 20px;
  border-color: black;
  border-radius: 10px;
  height: 200px;
  margin: 8rem;
  position: relative;
  width: 200px;
  -webkit-box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
          box-shadow: 0 6px 12px rgb(98, 98, 98), 0 3px 6px rgb(85, 85, 85);
  text-align: left;
}

.card > .img-container {
  border-radius: 10px;
  height: 100%;
  overflow: hidden;
  text-align: center;
  background: -webkit-radial-gradient(rgb(50, 100, 100), rgb(50, 150, 100));
  background: -o-radial-gradient(rgb(50, 100, 100), rgb(50, 150, 100));
  background: radial-gradient(rgb(50, 100, 100), rgb(50, 150, 100));
}

.card > .img-container > img {
  width: 80%
}

.card > .img-container:hover {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
  background: -webkit-radial-gradient(rgb(93, 194, 27), rgb(39, 31, 14));
  background: -o-radial-gradient(rgb(93, 194, 27), rgb(39, 31, 14));
  background: radial-gradient(rgb(93, 194, 27), rgb(39, 31, 14));
}


