.instructions {
  background-color: rgb(63, 63, 63); 
  padding: 20px 20px;
  opacity: 0.75;
  border-radius: 15px;
  margin-top: 150px;
  width: 100%;
  text-align: center;
  font-size: 5rem;
  text-decoration: underline;
  color: rgb(241, 241, 241);
}

.instructions:hover {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-transition: all 1s;
  -o-transition: all 1s;
  transition: all 1s;
}


